const emailjs = require('../vendor/emailjs.min');

emailjs.init('user_pVsXeBRyj0LMiCqzLjI32');

const {
  hideErrorMessage,
  hideSuccessMessage,
  showErrorMessage,
  showSuccessMessage,
} = require('./showMessage');

export const sendCotization = (serviceId, templateId, payload) =>
  emailjs
    .send(serviceId, templateId, payload)
    .then(() => {
      showSuccessMessage();
    })
    .catch(() => {
      showErrorMessage();
    })
    .finally(() => {
      setTimeout(() => {
        hideErrorMessage();
        hideSuccessMessage();
      }, 3500);
    });

export const sendOnlyEmail = (serviceId, templateId, payload) =>
  emailjs.send(serviceId, templateId, payload);
