const localForage = require('localforage');
const { showLoader, hideLoader } = require('../loader');
const { sendCotization } = require('./sendEmail');

export function cotizationRequest(submitEvent) {
  submitEvent.preventDefault();
  showLoader();
  const form = submitEvent.target;
  if (form !== null) {
    const fullname = form[0].value.trim();
    const email = form[1].value.trim();
    const phone = form[2].value.trim();

    if (!email || !fullname) {
      return;
    }
    sendCotization('service_0vw3hso', 'template_mqt13g8', {
      fullname,
      email,
      phone: phone || '',
    }).finally(() => {
      form.reset();
      hideLoader();
      localForage.clear();
    });
  } else {
    hideLoader();
  }
}
